import React, { useEffect, useState } from 'react'
import { Link, useHistory, us } from 'react-router-dom'
import { Row, Col, ListGroup, Image, Form, Button, Card, Table, Container } from 'react-bootstrap'
import { getBookingDatesDiffInDays } from "../utils/rental-utils.js"
import moment from 'moment'

import RentalCart from "../components/RentalCart"
import { Header } from './Header'
import { Footer } from './Footer'

import { getRentalCartItems } from "../utils/rental-cart-utils.js";

import { OverallPricing } from "./OverallPricing.js"

import { useNavigate } from 'react-router-dom'
import { GoBack } from "./GoBack.js"

import { addShippingAddressToLocalStorage } from "../utils/payment-utils.js";
import { Message } from './Message.js'

import { ShippingAddressDetails } from "./ShippingAddressDetails.js";

const CartPage = ({ match, location, history }) => {

  const rentalCartItems = getRentalCartItems();
  console.log(rentalCartItems);

  const navigate = useNavigate();

  const handleShippingAddressButtonClick = () => {
    navigate("/shippingaddress");
  }

  return (

    <>

      <Header></Header>

      <Container>

        <Row>
          <Col md={2}>
            <GoBack></GoBack>
          </Col>
        </Row>

        <Row>
          <Col>
            <RentalCart cartItems={rentalCartItems}></RentalCart>
          </Col>
        </Row>

        <Row>
          <Col>
            <OverallPricing rentalCartItems={rentalCartItems}></OverallPricing>
          </Col>

        </Row>


        <Row>

          <Col>

            <Card>
              <ListGroup variant='flush'>

                <ListGroup.Item>
                  <Button
                    type='button'
                    className='btn-block'
                    onClick={handleShippingAddressButtonClick}
                  >
                    Shipping Address Details
                  </Button>
                </ListGroup.Item>
              </ListGroup>

            </Card>

          </Col>
        </Row>

      </Container>

      <Footer></Footer>

    </>
  )
}

export { CartPage }
