
import { Navbar, Container, Nav, NavDropdown } from "react-bootstrap"
import { Link, useNavigate } from "react-router-dom"

import { hasUserLoggedIn, clearLocalStorage, getUserFromLocalStorage, hasAdminUserLoggedIn, getUserMode } from "../utils/user-utils.js";

const Header = () => {

  const userMode = getUserMode();
  console.log(userMode)

  const navigate = useNavigate();

  const handleLogout = () => {

    clearLocalStorage();
    document.location.href = '/login'

  }

  const handleMyOrders = () => {

    navigate("/myorders");
  }

  return (
    <header>

      <Navbar bg="light" expand="lg">
        <Container>
          <Navbar.Brand as={Link} to="/">Rentalz</Navbar.Brand>
          <Navbar.Toggle aria-controls="basic-navbar-nav" />
          <Navbar.Collapse id="basic-navbar-nav">
            <Nav className="me-auto">

              {
                (userMode === "REGULAR_USER") && (

                  <>

                    <Nav.Link as={Link} to="/cart">
                      <i className="fa-solid fa-cart-shopping"></i>
                      Cart
                    </Nav.Link>


                    <NavDropdown title={getUserFromLocalStorage().name} id="basic-nav-dropdown">


                      <NavDropdown.Item as={Link} to="/myorders" onClick={handleMyOrders}>
                        My Orders
                      </NavDropdown.Item>

                      <NavDropdown.Item as={Link} to="/logout" onClick={handleLogout}>
                        Logout
                      </NavDropdown.Item>

                    </NavDropdown>



                  </>

                )
              }

              {
                (userMode === "ADMIN_USER") && (

                  <>

                    <NavDropdown title="Users" id="basic-nav-dropdown">

                      <NavDropdown.Item as={Link} to="/users-manage">
                        Manage
                      </NavDropdown.Item>

                    </NavDropdown>

                    <NavDropdown title="Rental Products" id="basic-nav-dropdown">

                      <NavDropdown.Item as={Link} to="/rental-products-manage">
                        Manage
                      </NavDropdown.Item>

                    </NavDropdown>

                    <NavDropdown title="Orders" id="basic-nav-dropdown">

                      <NavDropdown.Item as={Link} to="/orders-manage">
                        Manage
                      </NavDropdown.Item>

                    </NavDropdown>


                    <NavDropdown title={getUserFromLocalStorage().name} id="basic-nav-dropdown">

                      <NavDropdown.Item as={Link} to="/logout" onClick={handleLogout}>
                        Logout
                      </NavDropdown.Item>

                    </NavDropdown>

                  </>
                )
              }


              {
                (userMode === "NO_USER") && (

                  <>
                    <Nav.Link as={Link} to="/login">
                      <i className="fa-solid fa-user"></i>
                      Login
                    </Nav.Link>
                  </>
                )
              }

            </Nav>
          </Navbar.Collapse>
        </Container>
      </Navbar>

    </header>
  )
}


export { Header }
