import { useEffect, useState } from "react"
import { Header } from "./Header"
import { Footer } from "./Footer"

import { Container, Row, Col, Table, ListGroup, ListGroupItem } from "react-bootstrap"
import axios from "axios";

import { getUserFromLocalStorage } from "../utils/user-utils.js"
import { Link } from "react-router-dom";

const MyOrdersPage = () => {


    const [myOrders, setMyOrders] = useState([]);

    useEffect(() => {

        const fetchMyOrders = async () => {

            const user = getUserFromLocalStorage();

            const { data } = await axios.get(
                `/api/myorders/${user._id}`, {
                headers: {
                    'Content-Type': 'application/json',
                },
            });

            console.log('My Orders');
            console.log(data);

            setMyOrders(data);
        }
        fetchMyOrders();
    }, [])


    return (
        <>
            <Header></Header>

            <div style={{ "minHeight": "85vh" }}>

                <Container>

                    <Table striped bordered hover responsive className='table-sm'>
                        <thead>
                            <tr>
                                <th>Order ID</th>
                                <td>Total Price</td>
                                <th>Shipping Address</th>
                                <th>Order Date</th>
                                <th>Status</th>
                            </tr>
                        </thead>

                        <tbody>
                            {myOrders.map((myOrder) => (

                                <tr key={myOrder._id}>
                                    <td>{myOrder._id}</td>

                                    <td>{myOrder.grandTotal}</td>
                                    <td>{myOrder.shippingAddress.address}</td>
                                    <td>{myOrder.paymentDate}</td>
                                    <td>{myOrder.status}</td>
                                </tr>
                            ))}
                        </tbody>

                    </Table>

                </Container>

            </div>

            <Footer></Footer>
        </>
    )
}

export { MyOrdersPage }