
const addUserToLocalStorage = (user) => {

    localStorage.setItem("RENTALZ_USER", JSON.stringify(user));
}

const clearLocalStorage = () => {

    localStorage.clear();
}

const getUserFromLocalStorage = () => {

    const item = localStorage.getItem("RENTALZ_USER");

    if (item){

        return JSON.parse(item);
    }
    return null;
}

const hasUserLoggedIn = () => {

    const item = localStorage.getItem("RENTALZ_USER");

    if (item){
        return true;
    }

    return false;
}

const hasAdminUserLoggedIn = () => {

    const item = localStorage.getItem("RENTALZ_USER");

    if (item){

        const user = JSON.parse(item);
        if (user && user.isAdmin){
            return true;
        }
    }

    return false;
}

const getUserMode = () => {

    // const adminUser = hasAdminUserLoggedIn();
    const userLoggedIn = hasUserLoggedIn();
  
    if (userLoggedIn){

        const user = getUserFromLocalStorage();
        if (user.isAdmin){
            return "ADMIN_USER";
        }else{
            return "REGULAR_USER";
        }

    }else{
        return "NO_USER"
    }
}

export {addUserToLocalStorage, hasUserLoggedIn, hasAdminUserLoggedIn, clearLocalStorage, getUserFromLocalStorage, getUserMode};
