import React, { useImperativeHandle, useState } from "react";
import { Row, Col, Form, ListGroup, ListGroupItem, Button } from "react-bootstrap"

import { getShippingAddressDetailsFromLocalStorage, addShippingAddressDetailsToLocalStorage } from "../utils/shipping-address-utils.js"
import { Message } from "./Message.js";

function ShippingAddressDetails(props, ref) {

  const shippingAddressDetails = getShippingAddressDetailsFromLocalStorage();

  const [shippingAddress, setShippingAddress]
    = useState(shippingAddressDetails.shippingAddress);

  const [landmarkDetails, setLandmarkDetails]
    = useState(shippingAddressDetails.landmarkDetails);

  const [contactNo, setContactNo]
    = useState(shippingAddressDetails.contactNo);

  const [deliveryInstructions, setDeliveryInstructions]
    = useState(shippingAddressDetails.deliveryInstructions);

  const [error, setError] = useState(null);

  useImperativeHandle(
    ref,
    () => {
      return {
        invokeValidation: () => {

          console.log('Invoke Validation');
          console.log(shippingAddress);
          console.log(landmarkDetails);
          console.log(contactNo);
          console.log(deliveryInstructions);

          addShippingAddressDetailsToLocalStorage({
            shippingAddress,
            landmarkDetails,
            contactNo,
            deliveryInstructions
          })

          console.log('Shipping Address')
          console.log(shippingAddress);

          if (!shippingAddress || shippingAddress.length === 0) {
            console.log('Validation Error')
            setError("Enter shipping address")
          } else {
            console.log('No Error');
          }
        }
      }
    },
    [shippingAddress, landmarkDetails, contactNo, deliveryInstructions]
  )

  return (

    <>

      <h2>Shipping Address</h2>

      <ListGroup>

        {
          error && (
            <Message messageType="error" content={error}></Message>
          )
        }

        <ListGroupItem>
          <Row>
            <Col>
              Shipping Address
            </Col>
          </Row>
          <Row>
            <Col>
              <Form.Control
                value={shippingAddress}
                onChange={(event) => {
                  setShippingAddress(event.target.value)
                }}
                as="textarea"
                placeholder="Your shipping address goes here"
                style={{ height: '150px' }}
              />
            </Col>
          </Row>
        </ListGroupItem>

        <ListGroupItem>
          <Row>
            <Col>
              Landmark Details(Optional)
            </Col>
          </Row>
          <Row>
            <Col>
              <Form.Control
                value={landmarkDetails}
                onChange={(event) => {
                  setLandmarkDetails(event.target.value)
                }}
                as="textarea"
                placeholder="Landmark details goes here"
                style={{ height: '80px' }}
              />
            </Col>
          </Row>
        </ListGroupItem>

        <ListGroupItem>

          <Row>
            <Col>Contact Details (Optional)</Col>
          </Row>

          <Row>
            <Col>
              <Form.Control type="text" placeholder="Contact Details" value={contactNo} onChange={(event) => {
                setContactNo(event.target.value)
              }} />
            </Col>
          </Row>
        </ListGroupItem>

        <ListGroupItem>
          <Row>
            <Col>Delivery Instructions (Optional)</Col>
          </Row>
          <Row>
            <Col>
              <Form.Control
                value={deliveryInstructions}
                onChange={(event) => {
                  setDeliveryInstructions(event.target.value)
                }}
                as="textarea"
                placeholder="Delivery instructions goes here"
                style={{ height: '80px' }}
              />
            </Col>
          </Row>
        </ListGroupItem>

      </ListGroup>

    </>
  )

}

export default React.forwardRef(ShippingAddressDetails)