
import { RENTALZ_ORDER_PRICING_DETAILS} from "./constants.js"

const addPricingDetailsToLocalStorage = (pricingDetails) => {

    let pricingDetailsAsJSON = JSON.stringify(pricingDetails);

    localStorage.setItem(RENTALZ_ORDER_PRICING_DETAILS, pricingDetailsAsJSON);
}

const getPricingDetailsFromLocalStorage = () => {

    const result =  localStorage.getItem(RENTALZ_ORDER_PRICING_DETAILS);

    if (result){
        return JSON.parse(result);
    }
    return '';
}


export {addPricingDetailsToLocalStorage, getPricingDetailsFromLocalStorage}