
import {RENTALZ_SHIPPING_ADDRESS_DETAILS} from "../utils/constants.js"

const getShippingAddressDetailsFromLocalStorage = () => {

  let shippingAddressDetails = {};

  const item = localStorage.getItem(RENTALZ_SHIPPING_ADDRESS_DETAILS);

  if (item){
    shippingAddressDetails = JSON.parse(item);
  }else{
    shippingAddressDetails.shippingAddress = '';
    shippingAddressDetails.landmarkDetails = '';
    shippingAddressDetails.contactNo = '';
    shippingAddressDetails.deliveryInstructions = '';
  }

  return shippingAddressDetails;
}


const addShippingAddressDetailsToLocalStorage = (shippingAddressDetails) => {

  const shippingAddressDetailsAsJSON = 
    JSON.stringify(shippingAddressDetails);

  localStorage.setItem(RENTALZ_SHIPPING_ADDRESS_DETAILS, shippingAddressDetailsAsJSON);

}

export {getShippingAddressDetailsFromLocalStorage, addShippingAddressDetailsToLocalStorage}