
import moment from "moment";
import {RENTALZ_RENTAL_CART_ITEMS} from "./constants.js";

const getRentalProductAvailabilityMessage = (product) => {

  return (product.availableQuantity === 0) ? "Not Available" : "Available in Stock"  
}

const getBookingDatesDiffInDaysv2 = (startDate, endDate) => {

  startDate = moment(startDate);
  endDate = moment(endDate);

  const diffInDays = endDate.diff(startDate, "days");
  return diffInDays;
}


const getBookingDatesDiffInDays = (bookingDates) => {

  console.log('Booking Dates ');
  console.log(bookingDates);

  let startDate = bookingDates[0];
  let endDate = bookingDates[1];

  startDate = moment(startDate);
  endDate = moment(endDate);

  const diffInDays = endDate.diff(startDate, "days");
  return diffInDays;
}


const calculateRentalAmount = (product, bookingDates, quantity) => {

  console.log(`Calculate Rental Amount`);

  const inDays = getBookingDatesDiffInDaysv2(
    bookingDates.startDate, bookingDates.endDate);
    
  console.log(`In Days -> ${inDays}`);

  const rentalPriceConfigurations = product.rentalPriceConfigurations;

  let noOfMonths = 0 
  let noOfDaysInMonthBalance = 0;
  if (inDays >=30){
    noOfMonths = inDays / 30
    noOfDaysInMonthBalance = (inDays - (30 * noOfMonths));

    console.log(`No of Months -> ${noOfMonths}`);
    console.log(`noOfDaysInMonthBalance -> ${noOfDaysInMonthBalance}`);

  }else{
    noOfDaysInMonthBalance = inDays;
  }

  let noOfWeeks = 0;
  let noOfDaysInWeekBalance = 0;
  if (noOfDaysInMonthBalance >= 7){

    noOfWeeks = noOfDaysInMonthBalance / 7;
    noOfDaysInWeekBalance = (noOfDaysInMonthBalance - (7 * noOfWeeks))

    console.log(`noOfWeeks -> ${noOfWeeks}`);
    console.log(`noOfDaysInWeekBalance -> ${noOfDaysInWeekBalance}`);
  }else{
    noOfDaysInWeekBalance = noOfDaysInMonthBalance;
  }

  let rentalAmount = 0;

  rentalPriceConfigurations.forEach( (rentalPriceConfiguration) => {

    let rentalValue = rentalPriceConfiguration.rentalValue;

    if (rentalPriceConfiguration.configurationName === "DAILY"){

      rentalAmount = rentalAmount + (noOfDaysInWeekBalance * rentalValue);
    }else if (rentalPriceConfiguration.configurationName === "WEEKLY"){

      rentalAmount = rentalAmount + (noOfWeeks * rentalValue);
    }else if (rentalPriceConfiguration.configurationName === "MONTHLY"){
      rentalAmount = rentalAmount + (noOfMonths * rentalValue);
    }

  })

  return rentalAmount * quantity;
}  


const asAmount = (value) => {

  const retValue = 0;

  if (value){
    return (value.toFixed(2));
  }else{
    return retValue.toFixed(2);
  }
}


export {
  getRentalProductAvailabilityMessage, 
  getBookingDatesDiffInDays, 
  calculateRentalAmount,
  asAmount,
  getBookingDatesDiffInDaysv2
}

