import { Footer } from "./Footer"
import { Header } from "./Header"

import { Container, Row, Col, ListGroup, ListGroupItem, Button } from "react-bootstrap"
import ShippingAddressDetails from "./ShippingAddressDetails"
import { GoBack } from "./GoBack"
import { OverallPricing } from "./OverallPricing"
import { useRef } from "react"

import { useNavigate } from "react-router-dom"

const ShippingAddressDetailsPage = () => {

  const shippingAddressDetailsRef = useRef()

  const navigate = useNavigate();

  const handlePaymentButtonClick = () => {

    shippingAddressDetailsRef.current.invokeValidation();
    navigate("/payment");
  }

  return (
    <>

      <Header></Header>

      <div style={{ "minHeight": "85vh" }}>

        <Container>

          <Row>
            <Col md={2}>
              <GoBack></GoBack>
            </Col>
          </Row>

          <Row>
            <Col md={7}>
              <ShippingAddressDetails ref={shippingAddressDetailsRef}></ShippingAddressDetails>
            </Col>

            <Col md={5}>
              <OverallPricing></OverallPricing>

              <ListGroupItem>
                <Button onClick={handlePaymentButtonClick} className="btn-block" type="button" >
                  Make Payment
                </Button>
              </ListGroupItem>

            </Col>
          </Row>
        </Container>

      </div>

      <Footer></Footer>
    </>
  )
}

export { ShippingAddressDetailsPage }