
import { Table, Row, Col, ListGroup, ListGroupItem } from "react-bootstrap";

import { calculateRentalAmount, asAmount } from "../utils/rental-utils.js"
import { useEffect, useState } from "react";

import { getRentalCartItems } from "../utils/rental-cart-utils.js"

import { getRentalCartItemsFromLocalStorage } from "../utils/rental-cart-utils.js"

import { addPricingDetailsToLocalStorage } from "../utils/payment-utils.js"

const OverallPricing = () => {

    const rentalCartItems = getRentalCartItemsFromLocalStorage();

    const calculatePrices = () => {

        let totalRentalChargesL = 0;
        let totalRefundableDepositL = 0;
        let totalDeliveryPickupChargesL = 0;
        let totalServiceTaxL = 0;
        let grandTotalL = 0;

        console.log('Calculate Total Rental Charges...')

        if (rentalCartItems !== null) {

            rentalCartItems.forEach((rentalCartItem) => {

                console.log('Rental Cart Item');
                console.log(rentalCartItem);

                totalRentalChargesL = totalRentalChargesL + calculateRentalAmount(
                    rentalCartItem.product, rentalCartItem.bookingDates, rentalCartItem.quantity);

                totalRefundableDepositL = totalRefundableDepositL + rentalCartItem.product.refundableDeposit;

                totalDeliveryPickupChargesL = totalDeliveryPickupChargesL
                    + rentalCartItem.product.deliveryPickupCharges;

            })


            totalServiceTaxL = ((totalRentalChargesL + totalRefundableDepositL + totalDeliveryPickupChargesL) * 30) / 100;
            grandTotalL = totalRentalChargesL + totalRefundableDepositL + totalDeliveryPickupChargesL + totalServiceTaxL;
        }

        let pricingDetails = {
            totalRentalCharges: totalRentalChargesL,
            totalRefundableDeposit: totalRefundableDepositL,
            totalDeliveryPickupCharges: totalDeliveryPickupChargesL,
            totalServiceTax: totalServiceTaxL,
            grandTotal: grandTotalL
        }
            ;

        addPricingDetailsToLocalStorage(pricingDetails);

        return pricingDetails;
    }

    const prices = calculatePrices();

    const [totalRentalCharges, setTotalRentalCharges] = useState(prices.totalRentalCharges);
    const [totalRefundableDeposit, setTotalRefundableDeposit] = useState(prices.totalRefundableDeposit);
    const [totalDeliveryPickupCharges, setTotalDeliveryPickupCharges] = useState(prices.totalDeliveryPickupCharges);
    const [totalServiceTax, setTotalServiceTax] = useState(prices.totalServiceTax);
    const [grandTotal, setGrandTotal] = useState(prices.grandTotal);

    return (

        <>

            <ListGroup>

                <h2>Pricing Details</h2>

                <ListGroupItem>
                    <Row>
                        <Col>Total Rental Charges</Col>
                        <Col>{asAmount(totalRentalCharges)}</Col>
                    </Row>
                </ListGroupItem>

                <ListGroupItem>
                    <Row>
                        <Col>Total Refundable Deposit</Col>
                        <Col>{asAmount(totalRefundableDeposit)}</Col>
                    </Row>
                </ListGroupItem>

                <ListGroupItem>
                    <Row>
                        <Col>Delivery / Pickup Charges</Col>
                        <Col>{asAmount(totalDeliveryPickupCharges)}</Col>
                    </Row>
                </ListGroupItem>

                <ListGroupItem>
                    <Row>
                        <Col>Service Tax</Col>
                        <Col>{asAmount(totalServiceTax)}</Col>
                    </Row>
                </ListGroupItem>

                <ListGroupItem>
                    <Row>
                        <Col>Grand Total</Col>
                        <Col><strong>{asAmount(grandTotal)}</strong></Col>
                    </Row>
                </ListGroupItem>

            </ListGroup>

        </>

    )
}

export { OverallPricing }