import { Container, ListGroup, ListGroupItem, Row, Col } from "react-bootstrap"
import { Footer } from "../Footer"
import { Header } from "../Header"

const AdminHomePage = () => {

  return (
    <>

      <Header></Header>

      <div style={{ "minHeight": "85vh" }}>

        <Container>

          <h3>Managment Activities</h3>

          <ListGroup>

            <ListGroupItem>
              <Row>
                <Col>Manage Users</Col>
              </Row>
            </ListGroupItem>

            <ListGroupItem>
              <Row>
                <Col>Manage Rental Products</Col>
              </Row>
            </ListGroupItem>

            <ListGroupItem>
              <Row>
                <Col>Manage Orders</Col>
              </Row>
            </ListGroupItem>

          </ListGroup>

        </Container>

      </div>

      <Footer></Footer>

    </>
  )
}

export { AdminHomePage }