

import { useState } from "react";

import { Row, Col, ListGroup, ListGroupItem, Image, Form, Button, Container } from "react-bootstrap"
import { Footer } from "./Footer"
import { Header } from "./Header"

import { useParams } from "react-router-dom";
import { useEffect } from "react";
import axios from "axios";
import { useNavigate } from "react-router-dom";

import {
    getRentalProductAvailabilityMessage,
    getBookingDatesDiffInDays,
    calculateRentalAmount,
} from "../utils/rental-utils.js"

import { constructQuantityArray } from "../utils/product-utils.js"

import { addRentalCartItemToStorage, getRentalCart, removeItem, updateItem, getDefaultBookingDates, getRentalCartItemDefaultValues } from "../utils/rental-cart-utils.js";
import { GoBack } from "./GoBack";

import { asAmount } from "../utils/rental-utils.js"
import { Message } from "./Message";

const DateRangePicker = require("rsuite/DateRangePicker").default


const ProductDetails = ({ product }) => {

    const rentalCartItemDefaultValues = getRentalCartItemDefaultValues(product);

    const [bookingDates, setBookingDates] = useState([
        rentalCartItemDefaultValues.bookingDates.startDate, rentalCartItemDefaultValues.bookingDates.endDate
    ]);
    const [totalNoOfDays, setTotalNoOfDays] = useState(rentalCartItemDefaultValues.totalNoOfDays);
    const [quantity, setQuantity] = useState(rentalCartItemDefaultValues.quantity);

    const [bookingDatesUserSelection, setBookingDatesUserSelection] = useState(false);
    const navigate = useNavigate();
    const [validationError, setValidationError] = useState(null)

    const addToCart = () => {

        console.log('Booking dates user selection');
        console.log(bookingDatesUserSelection);

        if (!bookingDatesUserSelection && totalNoOfDays === 0) {
            setValidationError(true);
        } else {

            let bookingDatesInput = {
                startDate: bookingDates[0],
                endDate: bookingDates[1]
            }

            addRentalCartItemToStorage(product, quantity, bookingDatesInput, totalNoOfDays);
            navigate("/cart");
        }
    }


    return (


        <Container>

            <Row>
                <Col>{validationError && (
                    <Message messageType="error" content="Please select booking dates"></Message>
                )}</Col>
            </Row>

            <Row>
                <Col>
                    <ListGroup>
                        <ListGroupItem>
                            <h3>{product.name}</h3>
                        </ListGroupItem>
                    </ListGroup>
                </Col>
            </Row>

            <Row>
                <Col>
                    <ListGroup>
                        <ListGroupItem>
                            {product.description}
                        </ListGroupItem>
                    </ListGroup>
                </Col>
            </Row>


            <Row>
                <Col md={4}>
                    <Image src={product.image} alt={product.name} fluid></Image>
                </Col>

                <Col md={8}>

                    <Row>

                        <ListGroup>
                            <ListGroupItem>
                                <Row>
                                    <Col>Status</Col>
                                    <Col>{getRentalProductAvailabilityMessage(product)}</Col>
                                </Row>
                            </ListGroupItem>

                            <ListGroupItem>
                                <Row>
                                    <Col>Booking Dates</Col>
                                    <Col>


                                        <DateRangePicker format="yyyy-MM-dd" value={bookingDates} placeholder="Select Booking Dates" onChange={(newBookingDates) => {

                                            const diffInDays = getBookingDatesDiffInDays(newBookingDates);
                                            console.log('Diff in days ' + diffInDays);

                                            setBookingDates(newBookingDates);
                                            setTotalNoOfDays(diffInDays);

                                            setBookingDatesUserSelection(true);
                                        }}>

                                        </DateRangePicker>
                                    </Col>
                                </Row>
                            </ListGroupItem>

                            <ListGroupItem>
                                <Row>
                                    <Col>Total No of Days</Col>
                                    <Col>{totalNoOfDays}</Col>
                                </Row>
                            </ListGroupItem>

                            <ListGroupItem>
                                <Row>
                                    <Col>Quantity</Col>
                                    <Col>
                                        <Form.Control
                                            as='select'
                                            value={quantity}
                                            onChange={(event) => {

                                                setQuantity(event.target.value)
                                            }}

                                        >

                                            {

                                                constructQuantityArray(product).map((quantityValue) => {

                                                    return (
                                                        <option key={quantityValue} value={quantityValue}>
                                                            {quantityValue}</option>
                                                    )
                                                })
                                            }

                                        </Form.Control>
                                    </Col>
                                </Row>
                            </ListGroupItem>
                        </ListGroup>


                    </Row>

                    <Row>
                        <ListGroup>

                            <ListGroupItem>
                                <Row>
                                    <Col>
                                        Rental Prices
                                    </Col>
                                    <Col>

                                        {
                                            (product.rentalPriceConfigurations != null) && (

                                                <ListGroup horizontal>
                                                    {
                                                        product.rentalPriceConfigurations.map((rentalPriceConfiguration) => {

                                                            let configurationName = rentalPriceConfiguration.configurationName;

                                                            let configurationDisplay = configurationName === "DAILY" ? "/ Day" : (
                                                                configurationName === "WEEKLY" ? " / Week" : " / Monthly"
                                                            )

                                                            let rentalValue = asAmount(rentalPriceConfiguration.rentalValue);

                                                            // Rs 100 / Day 
                                                            // Rs 2000 / Week
                                                            let rentalPriceDisplay = "Rs " + rentalValue + configurationDisplay;

                                                            return (
                                                                <ListGroupItem key={configurationName}>{rentalPriceDisplay}</ListGroupItem>
                                                            )

                                                        })
                                                    }
                                                </ListGroup>
                                            )
                                        }
                                    </Col>
                                </Row>
                            </ListGroupItem>

                            <ListGroupItem>
                                <Row>
                                    <Col>Refundable Deposit</Col>
                                    <Col>{asAmount(product.refundableDeposit)}</Col>
                                </Row>
                            </ListGroupItem>

                            <ListGroupItem>
                                <Row>
                                    <Col>Delivery/Pickup Charges</Col>
                                    <Col>{asAmount(product.deliveryPickupCharges)}</Col>
                                </Row>
                            </ListGroupItem>

                            <ListGroupItem>
                                <Button onClick={addToCart} className="btn-block" type="button" disabled={product.availableQuantity === 0}>
                                    Add to Cart
                                </Button>
                            </ListGroupItem>
                        </ListGroup>
                    </Row>

                </Col>
            </Row>

        </Container>

    )
}

export { ProductDetails }