
import {getUserFromLocalStorage} from "./user-utils"
import {getRentalCartItemsFromLocalStorage} from "./rental-cart-utils.js";
import {getPricingDetailsFromLocalStorage} from "./payment-utils.js"
import {getShippingAddressDetailsFromLocalStorage} from "./shipping-address-utils.js"

import {RENTALZ_SHIPPING_ADDRESS_DETAILS, RENTALZ_RENTAL_CART_ITEMS, RENTALZ_ORDER_PRICING_DETAILS} from "./constants.js"

import axios from "axios";

const createNewOrder = async (paymentOutcome) => {


    const rentalCartItems = getRentalCartItemsFromLocalStorage();

    const orderItems = [];

    rentalCartItems.forEach( (rentalCartItem) => {


        console.log('Inside create Order -> Rental order Item')
        console.log(JSON.stringify(rentalCartItem));

        let orderItem = {};

        orderItem.productReference = rentalCartItem.product._id;
        orderItem.quantity = rentalCartItem.quantity;

        orderItem.bookingDates = {
            startDate : rentalCartItem.bookingDates.startDate,
            endDate: rentalCartItem.bookingDates.endDate
        }

        orderItems.push(orderItem);
    })



    const { data } = await axios.post(`/api/orders`, {
        userReference : getUserFromLocalStorage()._id,
        orderItems: orderItems,
        shippingAddressDetails : getShippingAddressDetailsFromLocalStorage(),
        pricingDetails: getPricingDetailsFromLocalStorage(),
        paymentOutcome : paymentOutcome,
        paymentDate: new Date()

    }, {
        headers: {
            'Content-Type': 'application/json',
        }
    })

    return data;
}

const clearLocalStorageAfterOrderPlacement = () => {

    localStorage.removeItem(RENTALZ_SHIPPING_ADDRESS_DETAILS);
    localStorage.removeItem(RENTALZ_RENTAL_CART_ITEMS);
    localStorage.removeItem(RENTALZ_ORDER_PRICING_DETAILS);

}


export {createNewOrder, clearLocalStorageAfterOrderPlacement}