
import React, { useEffect, useState } from 'react'
import { Link, useHistory, } from 'react-router-dom'
import { Row, Col, ListGroup, Image, Form, Button, Card, Table } from 'react-bootstrap'
import moment from 'moment'
import { getBookingDatesDiffInDays, calculateRentalAmount, asAmount } from "../utils/rental-utils.js"

import { constructQuantityArray } from "../utils/product-utils.js"

import { setRentalCartItemsToLocalStorage } from "../utils/rental-cart-utils.js"
import { Message } from './Message.js'

const RentalCart = ({ cartItems }) => {


    const [rentalCartItems, setRentalCartItems] = useState(cartItems);


    const updateQuantity = (product, newQuantity) => {

        const updatedRentalCartItems = rentalCartItems.map((rentalCartItem) => {

            if (rentalCartItem.product._id === product._id) {

                let updatedRentalCartItem = rentalCartItem;
                updatedRentalCartItem.quantity = newQuantity;
                return updatedRentalCartItem;
            } else {
                return rentalCartItem
            }

        })

        setRentalCartItems(updatedRentalCartItems);
        setRentalCartItemsToLocalStorage(rentalCartItems);
    }

    const displayBookingDates = (bookingDates) => {

        const startDate = moment(bookingDates.startDate);
        const endDate = moment(bookingDates.endDate);

        return startDate.format("MMM Do YYYY") + ' To ' + endDate.format("MMM Do YYYY");
    }


    return (

        <>

            <h2>My Rental Cart</h2>

            {rentalCartItems != null && rentalCartItems.length === 0 ? (

                <Message messageType="info" content="Your cart is empty"></Message>
            ) : (

                <Table striped bordered hover responsive className='table-sm'>
                    <thead>
                        <tr>
                            <th>Product Title</th>
                            <td>Booking Dates</td>
                            <th>Total No of Days</th>
                            <th>Quantity</th>
                            <th>Refundable Deposit</th>
                            <th>Delivery / Pickup Charges</th>
                            <th>Rental Amount</th>
                            <th></th>
                        </tr>
                    </thead>

                    <tbody>
                        {rentalCartItems.map((cartItem) => (

                            <tr key={cartItem.product._id}>
                                <td><Link to={`/product/${cartItem.product._id}`}>{cartItem.product.name}</Link></td>
                                <td>{displayBookingDates(cartItem.bookingDates)}</td>
                                <td>{cartItem.totalNoOfDays}</td>

                                <td>

                                    <Form.Control
                                        as='select'
                                        value={cartItem.quantity}
                                        onChange={(event) => {
                                            updateQuantity(cartItem.product, event.target.value)
                                        }}

                                    >

                                        {

                                            constructQuantityArray(cartItem.product).map((quantityValue) => {

                                                return (
                                                    <option key={quantityValue} value={quantityValue}>
                                                        {quantityValue}</option>
                                                )
                                            })
                                        }

                                    </Form.Control>


                                </td>

                                <td>${asAmount(cartItem.product.refundableDeposit)}</td>
                                <td>${asAmount(cartItem.product.deliveryPickupCharges)}</td>
                                <td>${asAmount(calculateRentalAmount(cartItem.product, cartItem.bookingDates, cartItem.quantity))}</td>
                            </tr>

                        ))}
                    </tbody>

                </Table>
            )}
        </>
    )
}

export default RentalCart
