
import {RENTALZ_RENTAL_CART_ITEMS} from "./constants.js";

const getRentalCartItems = () => {

    let rentalCartItems = new Array();

    let item = localStorage.getItem(RENTALZ_RENTAL_CART_ITEMS);
    console.log('Item is ');
    console.log(item);

    if (item){
        rentalCartItems = JSON.parse(item);
 
        return rentalCartItems;
    }else{
        return rentalCartItems;
    }
}

const getRentalCartItemsFromLocalStorage = () => {

    return getRentalCartItems();
}


const addRentalCartItemToStorage = (product, quantity, bookingDates, totalNoOfDays) => {

    let rentalCartItems = getRentalCartItems();
    const existingRentalCartItem = findRentalCartItem(rentalCartItems, product);

    if (existingRentalCartItem){

        console.log('Rental Cart Item already existing...')
        
        existingRentalCartItem.quantity = quantity;
        existingRentalCartItem.bookingDates = {
            startDate: bookingDates.startDate,
            endDate: bookingDates.endDate
        }
        existingRentalCartItem.totalNoOfDays = totalNoOfDays;
    }else{

        console.log('NEW Rental Cart Item ...')

        let newRentalCartItem = {
            product : product,
            quantity: quantity,
            bookingDates : {
                startDate: bookingDates.startDate,
                endDate: bookingDates.endDate
            },
            totalNoOfDays : totalNoOfDays   
        }

        rentalCartItems.push(newRentalCartItem);
    }

    console.log('Add Rental Cart Item to Storage');
    console.log(rentalCartItems);

    setRentalCartItemsToLocalStorage(rentalCartItems);
}

const getRentalCartItemDefaultValues = (product) => {

    const rentalCartItems = getRentalCartItems();

    if (rentalCartItems){

        console.log('getRentalCartItem')

        console.log(`Product ${product._id}`);
        console.log('Rental Cart Items');
        console.log(JSON.stringify(rentalCartItems));
    
        let existingRentalCartItem = findRentalCartItem(rentalCartItems, product);
    
    
        if (existingRentalCartItem){

            console.log('Existing Rental Cart item is ');
            console.log(existingRentalCartItem);
        
            return existingRentalCartItem;    
        }else{
            return getRentalCartItemInitialValues();
        }
    }else{

        return getRentalCartItemInitialValues();
    }
}


const getRentalCartItemInitialValues = () => {

    let rentalCartItemInitialValues = {
        bookingDates : {
            startDate : new Date(),
            endDate : new Date()
        },

        quantity: 1,
        totalNoOfDays : 0
    }

    return rentalCartItemInitialValues;
}

const findRentalCartItem = (rentalCartItems, product) => {

    console.log('FIND RENTAL CART ITEM')
    let existingRentalCartItem = rentalCartItems.find( (rentalCartItem) => {

        if (rentalCartItem.product._id === product._id){

            let returnRentalCartItem = rentalCartItem;

            try{
                returnRentalCartItem.bookingDates = {
    
                    startDate : Date.parse(rentalCartItem.bookingDates.startDate),
                    endDate : Date.parse(rentalCartItem.bookingDates.endDate)    
                }    
            }catch(error){
                console.log('DATE PARSING FAILURE');
                console.log(error);
            }
    
            return returnRentalCartItem;
        }
    })

    return existingRentalCartItem;
}

const setRentalCartItemsToLocalStorage = (rentalCartItems) => {

    const rentalCartItemsAsJSON = JSON.stringify(rentalCartItems);
    localStorage.setItem(RENTALZ_RENTAL_CART_ITEMS, rentalCartItemsAsJSON);

}

const updateQuantity = (product, newQuantity) => {

    let rentalCartItems = getRentalCartItems();
    const existingRentalCartItem = findRentalCartItem(rentalCartItems, product)

    if (existingRentalCartItem){
        existingRentalCartItem.quantity = newQuantity;
        setRentalCartItemsToLocalStorage(rentalCartItems);
    }else{
        throw new Error("Existing Rental Cart Item expected, but not found")
    }

}

export {getRentalCartItems, getRentalCartItemDefaultValues, updateQuantity, setRentalCartItemsToLocalStorage, getRentalCartItemsFromLocalStorage, addRentalCartItemToStorage}

