
import { Card, Button } from "react-bootstrap"
import { Link, useNavigate } from "react-router-dom"
import { getRentalProductAvailabilityMessage } from "../utils/rental-utils.js";

import { hasUserLoggedIn } from "../utils/user-utils.js";

const Product = ({ product }) => {

  const navigate = useNavigate();

  return (
    <div>

      <Card style={{ width: '18rem' }}>

        <Card.Link as={Link} to={`/product/${product._id}`}>
          <Card.Img variant="top" src={product.image} />
        </Card.Link>

        <Card.Body>

          <Card.Link as={Link} to={`/products/${product._id}`}>
            <Card.Title>{product.name}</Card.Title>
          </Card.Link>

          <Card.Subtitle className="mb-2 text-muted">
            {getRentalProductAvailabilityMessage(product)}</Card.Subtitle>
          <Card.Text>
            {product.description}
          </Card.Text>

          {
            hasUserLoggedIn() && (

              <Button variant="primary" disabled={product.availableQuantity === 0} onClick={(event) => {

                let url = `/product/${product._id}`;
                navigate(url);
              }}>
                Add to Cart
              </Button>

            )
          }


        </Card.Body>
      </Card>

    </div>
  )
}

export { Product }