import React, { useState, useEffect } from 'react'
import axios from 'axios'
import { PayPalButton } from 'react-paypal-button-v2'
import { Link } from 'react-router-dom'
import { Row, Col, ListGroup, Image, Card, Button, Container, ListGroupItem, Alert } from 'react-bootstrap'

import RentalCart from './RentalCart'
import { OverallPricing } from './OverallPricing'

import { getRentalCartItems } from "../utils/rental-cart-utils.js";

import { getShippingAddressFromLocalStorage } from "../utils/payment-utils.js"

import { createNewOrder } from "../utils/order-utils.js"

import { useNavigate } from 'react-router-dom'
import { Header } from './Header'
import { Footer } from './Footer'
import { GoBack } from './GoBack'

import { Message } from "./Message.js"

import { clearLocalStorageAfterOrderPlacement } from "../utils/order-utils.js";
import { OrderDetails } from "./OrderDetails"

import { getUserFromLocalStorage } from "../utils/user-utils.js";

const PaymentPage = ({ match, history }) => {

    const user = getUserFromLocalStorage();

    const [order, setOrder] = useState(0);

    const navigate = useNavigate();

    const rentalCartItems = getRentalCartItems();
    console.log(rentalCartItems);

    const [paymentDone, setPaymentDone] = useState(false);


    useEffect(() => {

        const addPayPalScript = async () => {

            const { data: clientId } = await axios.get('/api/rentalproducts/paypal/configuration')
            const script = document.createElement('script')
            script.type = 'text/javascript'
            script.src = `https://www.paypal.com/sdk/js?client-id=${clientId}`
            script.async = true
            script.onload = () => {
            }
            document.body.appendChild(script)
        }

        if (!paymentDone) {
            addPayPalScript()
        }
    }, [
    ])

    const paypalHandleSuccess = async (paymentOutcome) => {

        console.log(paymentOutcome);

        const orderResponse = await createNewOrder(paymentOutcome);
        setOrder(orderResponse);

        if (orderResponse.status == 'COMPLETED') {

            clearLocalStorageAfterOrderPlacement();

            setPaymentDone(true);
        } else {

            console.log('Order processing has failed...')
        }
    }

    return (
        <>

            <Header></Header>

            <div style={{ "minHeight": "85vh" }}>

                <Container>

                    <Row>
                        <Col md={2}>
                            <GoBack></GoBack>
                        </Col>
                    </Row>


                    <Row>
                        <Col>
                            {
                                paymentDone ? (
                                    <Message messageType="info"
                                        content="Payment is DONE for this order. "
                                    ></Message>
                                ) : (
                                    <Message messageType="info" content="Payment is PENDING for this order"></Message>
                                )
                            }
                        </Col>
                    </Row>

                    <Row>
                        <Col md={8}>
                            {
                                !paymentDone ? (
                                    <OverallPricing rentalCartItems={rentalCartItems}></OverallPricing>
                                ) : (
                                    <OrderDetails order={order} user={user}></OrderDetails>
                                )
                            }
                        </Col>

                        <Col md={4}>

                            {!paymentDone && (

                                <>
                                    <h3>Make Payment</h3>

                                    <PayPalButton
                                        amount={100}
                                        onSuccess={paypalHandleSuccess}
                                    />
                                </>

                            )}

                        </Col>
                    </Row>

                </Container>

            </div>

            <Footer></Footer>
        </>
    )
}

export { PaymentPage }
