import { Container, Row, Col, ListGroup, ListGroupItem } from "react-bootstrap"

const OrderDetails = ({ order, user }) => {

    const calculateTotalNoOfProducts = () => {

        let total = 0;
        order.orderItems.forEach((orderItem) => {

            total = total + orderItem.length
        })
        return total;
    }

    return (
        <>
            <Container>

                <ListGroup>

                    <ListGroupItem>
                        <Row>
                            <Col>Order #</Col>
                            <Col>{order._id}</Col>
                        </Row>
                    </ListGroupItem>

                    <ListGroupItem>
                        <Row>
                            <Col>Order Placed By</Col>
                            <Col>{user.name}</Col>
                        </Row>
                    </ListGroupItem>

                    <ListGroupItem>
                        <Row>
                            <Col>Total Product Types</Col>
                            <Col>{order.orderItems.length}</Col>
                        </Row>
                    </ListGroupItem>

                    <ListGroupItem>
                        <Row>
                            <Col>Total # of Products</Col>
                            <Col>{calculateTotalNoOfProducts()}</Col>
                        </Row>
                    </ListGroupItem>

                    <ListGroupItem>
                        <Row>
                            <Col>Shipping Address</Col>
                            <Col>{order.shippingAddressDetails.shippingAddress}</Col>
                        </Row>
                    </ListGroupItem>

                    <ListGroupItem>
                        <Row>
                            <Col>Total Price</Col>
                            <Col>{order.pricingDetails.grandTotal}</Col>
                        </Row>
                    </ListGroupItem>

                    <ListGroupItem>
                        <Row>
                            <Col>Payment Date</Col>
                            <Col>{order.paymentDate}</Col>
                        </Row>
                    </ListGroupItem>

                    <ListGroupItem>
                        <Row>
                            <Col>Paypal Reference</Col>
                            <Col>{order.paymentOutcome.reference}</Col>
                        </Row>
                    </ListGroupItem>

                    <ListGroupItem>
                        <Row>
                            <Col>Order Status</Col>
                            <Col>{order.status}</Col>
                        </Row>
                    </ListGroupItem>

                </ListGroup>
            </Container>
        </>
    )
}

export { OrderDetails }